<template>
  <div>
    <van-swipe
      :autoplay="3000"
      @change="onChange">
      <van-swipe-item
        v-for="item in swipes"
        :key="item.id">
        <img :src="item.cover" @click="onClick(item.id)"/>
      </van-swipe-item>
    </van-swipe>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell
        v-for="item in list"
        :key="item.id"
        @click="onClick(item.id)">
        <van-card
          :price="item.price"
          :desc="item.description"
          :title="item.title"
          :thumb="item.cover"
        />
      </van-cell>
    </van-list>
    <van-tabbar v-model="active" @change="onChangeTab">
      <van-tabbar-item icon="shop">精选</van-tabbar-item>
      <van-tabbar-item icon="cart">购物车</van-tabbar-item>
      <van-tabbar-item icon="contact">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {
  Swipe,
  SwipeItem,
  Lazyload,
  Card,
  List,
  Cell,
  Tabbar,
  TabbarItem,
} from 'vant';

export default {
  name: 'Index',
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Lazyload.name]: Lazyload,
    [Card.name]: Card,
    [List.name]: List,
    [Cell.name]: Cell,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
  },
  data() {
    return {
      swipes: [],
      list: [],
      loading: false,
      finished: false,
      active: 0,
    };
  },

  created() {
    this.initList();
  },
  methods: {
    initList() {
      // eslint-disable-next-line
      console.log(this.$store.state.cart);
      // eslint-disable-next-line
      console.log('首页轮播初始化');
      // 请求商品列表
      this.$ajax
        .get('/api/products/choiceness')
        .then((response) => {
          // eslint-disable-next-line
          console.log(response.data.items);
          this.swipes = this.assembleList(response.data.items);
        });
      // eslint-disable-next-line
      console.log('首页精选初始化');
      // 请求商品列表
      this.$ajax
        .get('/api/products/choiceness')
        .then((response) => {
          // eslint-disable-next-line
          console.log(response.data.items);
          this.list = this.assembleList(response.data.items);
        });
    },
    assembleList(items) {
      return items.map(item => ({
        id: item.id,
        title: item.title,
        price: item.price,
        cover: item.cover,
      }));
    },
    onChangeTab(index) {
      // eslint-disable-next-line
      console.log(index);
      switch (index) {
        case 0:
          return this.$router.push({ path: '/' });
        case 1:
          return this.$router.push({ path: 'cart' });
        case 2:
          return this.$router.push({ path: '/user' });
        default:
          return this.$router.push({ path: '/' });
      }
    },
    onChange(index) {
      // eslint-disable-next-line
      console.log(index);
      this.current = index;
    },
    onClick(id) {
      // eslint-disable-next-line
      console.log(this.current + 'fff');
      this.$router.push({ path: `/goods/${id}` });
    },
    onLoad() {
      this.loading = false;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



